import React from 'react';
import figurasHome01 from '../assets/images/figurasHome01.jpg';
import { Container, Row, Col } from 'react-bootstrap';

function ContentHome(props) {

    return(
        <Container fluid={true}>
            <Row className="justify-content-center">
                <Col md={8}>
                    <img className="home-content-image" alt="timer" src={figurasHome01} />
                </Col>
                <Col md={4} className="home-content">
                    {props.children}
                </Col>
            </Row>
            <Row className="justify-content-center">
                <Col md={8}>
                    <img className="home-content-image" alt="timer" src={figurasHome01} />
                </Col>
                <Col md={4} className="home-content">
                    {props.children}
                </Col>
            </Row>
        </Container>
    );

}

export default ContentHome;